@import 'mixins';

#load-episode {
  margin-top: 10rem;
  @include load-container(7);
}

#episode-top {
  h3 {
    @include top-header;
  }

  img {
    @include single-image;
  }
}

#episode-bottom {
  @include sub-display;
}

#episode-button-box {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin-top: 1rem;
}
